<template>
  <div>
    <div id="header">
      <header-nav></header-nav>
    </div>
    <div class="content mt30 clearfix padding-top-60" id="content">
      <!--左边区域-->
      <div id="left" class="mr30 mt30 mb30">
        <!--封面图-->
        <div class="bg-img mainImg br5"
             :style="{backgroundImage:'url('+osshost+ menuDetail.cpimg +detailpic+')'}"></div>
        <div class="menu_info mt10">
          <div class="name">{{ menuDetail.title }}</div>
          <div class="info clearfix">
            <div class="mainColor left">
            </div>
            <div class="grayColor">
              <span class="mr10"><i class="iconfont grayColor"> &#xe65d;</i>{{menuDetail.collections}}</span>
              <span><i class="iconfont mr10 grayColor">&#xe661;</i>{{menuDetail.visits}}</span>
              <div @click="collectMenu()" class="btn-sc">
                <el-button v-if="menuDetail.iscollectioned" class="el-icon-close" size="small" round>取消收藏</el-button>
                <el-button v-else="" class="el-icon-plus" size="small" type="warning" round>收藏菜谱</el-button>
              </div>
            </div>
          </div>
        </div>
        <!--用户信息-->
        <div>
          <ul class="dr_list dr_list2">
            <li class="clearfix mb15">
              <div class="left headicon bg-img br50" @click="navToIndex(menuDetail.userid)"
                   :style="{backgroundImage:'url('+osshost+ menuDetail.faceimg +userface+')'}">
              </div>
              <div class="info">
                <h2 class="text-bold text-df">{{menuDetail.nickname}}</h2>
              </div>
              <a class="right" v-if="menuDetail.relationship == 0 || menuDetail.relationship == 2"
                 @click="attentionUser(menuDetail.userid)">
                <el-button class="el-icon-plus" size="small" type="warning" round>关注</el-button>
              </a>
              <a class="right" v-else @click="unAttentionUser(menuDetail.userid)">
                <el-button class="el-icon-close" size="small" round>取消关注</el-button>
              </a>
            </li>
          </ul>
          <div class="text-df">{{menuDetail.story}}</div>
        </div>
        <!--  食材配料    -->
        <div class="mt50">
          <h1>食材配料</h1>
          <div class="grid col-2 border mt15">
            <div class="clearfix text-df" v-for="(item,index) in foodList" :key="index">
              <span class="left text-bold">{{item.name}}</span>
              <span class="right">{{item.num}}</span>
            </div>
          </div>
        </div>
        <!--  制作步骤    -->
        <div class="mt50">
          <h1>制作步骤</h1>
          <div v-for="(item,index) in menuDetail.step" :key="index" class="mt15">
            <div class="mainColor height-30"><span class="text-df textTop">步骤</span><span
              class="text-xsl">{{item.sort}}</span>
            </div>
            <div class="clearfix mt15">
              <div class="left" v-if="item.stepImg!=''">
                <div class="bg-img cover xxl br5"
                     :style="{backgroundImage:'url('+osshost+ item.stepImg +listpic+')'}"></div>
              </div>
              <div class="left ml15 text-df" style="width:365px">
                {{item.description}}
              </div>
            </div>
          </div>
        </div>
        <!--  小贴士    -->
        <div class="mt50">
          <h1><i class="iconfont mainColor text-xl mr10">&#xe674;</i>小贴士</h1>
          <div class="text-df mt20">
            <p class="mb35">{{menuDetail.tip}}</p>
            <p class="grayColor">创建时间：{{menuDetail.pushtime}}</p>
            <p>本菜谱的做法由<span class="mainColor">{{menuDetail.nickname}}</span>编写，未经授权不得转载</p>
          </div>
        </div>
        <!--  大家的学习成果    -->
        <div class="mt45">
          <h1>大家的学习成果
            <div  @click="dialogVisible = true" class="more">更多{{menuDetail.shows.length}}<i class="iconfont mainColor">&#xe654;</i>
            </div>
          </h1>
          <ul class="like-list2 like-list5 clearfix">
            <li class="item" v-for="(item, index) in menuDetail.shows.slice(0,4)" :key="index"
                @click="navToDetail(item.id)">
              <div class="cover br5 bg-img" :style="{ backgroundImage: 'url(' + osshost +item.dtimg +listpic+ ')' }">
              </div>
              <div>
                <div class="name text-tips">{{ item.description }}</div>
                <div class="author">
                  <img :src="osshost + item.faceimg +userface" class="headicon br50">
                  <span class="nickname text-lips" style="max-width: 238px">{{item.nickname}}</span>
                </div>
              </div>
            </li>
          </ul>
          <div class="text-center mt30">
            <router-link :to="{path:'/publishDt?cpid='+ cpid}">
               <button class="cu-btn bg-yellow br5" >上传我的作品</button>
            </router-link>

          </div>
        </div>
        <!--面包屑导航  -->
        <div class="">
          <p class="fcc text-lips">您当前的位置：<a href="/" title="">首页</a> \ {{ menuDetail.title }}</p>
        </div>
        <!-- 评论 -->
        <div class="mt50">
          <h1 id="reply">评论<el-button class="right" type="warning" @click="comment(0, 0)">发表评论</el-button></h1>
          <div class="mt30">

            <el-dialog
              title="评论回复"
              :visible.sync="replyShow"
              width="600px"
              :before-close="handleClose">
              <form class="cu-form-group">
                <textarea :placeholder="input_placeholder" v-model="message"></textarea>
                <div class="clearfix">
                  <button class="cu-btn sm bg-yellow right br5 mt10"
                          @click="postComment()"> 发表评论
                  </button>
                </div>
              </form>
            </el-dialog>
            <div>
              <div>
                <ul class="dr_list">
                  <li class="clearfix mb15" v-for="(item, index) in commentList" :key="index">
                    <div class="left headicon bg-img br50"
                    @click="navToIndex(item.userid)"
                         :style="{backgroundImage:'url('+ osshost + item.faceimg + userface +')'}">
                    </div>
                    <div class="info" style="width:640px">
                      <h4 class="text-bold text-df">{{ item.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="item.userid == menuDetail.userid">作者</span></h4>
                      <p class="grayColor">{{ item.pushtime }} <span class="mainColor"
                                                                     @click="reply(item.commentid,item.userid,item.id,item.nickname)">回复</span></p>

                      <div>
                        <p class="text-df text-height-lg"> {{ item.message }}</p>
                      </div>
                      <ul class="dr_list">
                        <li class="clearfix mb15" v-for="(child, childindex) in item.subreplylist" :key="childindex">
                          <div class="left headicon bg-img br50"
                               :style="{backgroundImage:'url('+ osshost + child.faceimg + userface +')'}"
                               @click="navToIndex(child.userid)">
                          </div>
                          <div class="info" style="width:440px">
                            <h4 class="text-bold text-df">{{ child.nickname }}<span class="text-12 grayColor user-img-boder ml10" v-if="child.userid == menuDetail.userid">作者</span></h4>
                            <p class="grayColor">{{ child.pushtime }} <span class="mainColor"
                                                                            @click="reply2(child.id, item.userid, child.userid, child.commentid, child.nickname)">回复</span>
                            </p>
                            <div>
                              <p class="text-14 grayColor" v-if="child.bereplynickname">回复:{{ child.bereplynickname }}</p>
                              <p class="text-df text-height-lg"> {{ child.message }}</p>
                            </div>
                          </div>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
                <div class="text-center mainColor mb15" v-if="loadMore" @click="loadMorep()">点击加载更多</div>
                <div class="text-center grayColor mb15" v-else>没有评论了</div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!--右边区域-->
      <el-dialog
        :title="menuDetail.title+'相关作品'"
        :visible.sync="dialogVisible"
        width="1040px"
        :before-close="handleClose">
        <ul class="like-list2 clearfix">
          <li class="item" v-for="(item, index) in mainMenuList" :key="index">
            <div class="cover bg-img" :style="{ backgroundImage: 'url(' + osshost +item.dtimg +listpic+ ')' }"
                 @click="navToDetail(item.id)">
            </div>
            <div>
              <div>
                <div class="name text-tips text-cut-two">{{ item.description }}</div>
                <div class="author">
                  <img :src="osshost + item.faceimg +userface" class="headicon br50" @click="navToIndex(item.userid)">
                  <span class="nickname text-lips" style="max-width: 238px">{{item.nickname}}</span>
                  <span class="right text-xm el-icon-lollipop margin-right-xs block">{{  item.likecount}}</span>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <infinite-loading @infinite="infiniteHandler" class="mt50 mb60">
          <div slot="spinner"><i class="el-icon-loading"></i>小弟拼命加载中...</div>//加载中的文字
          <div slot="no-more" class="el-icon-food grayColor">已加载完毕!</div>//加载完毕的文字
          <div slot="no-results" class="el-icon-coffee-cup grayColor">暂无数据:(</div>//没有数据的文字
        </infinite-loading>
      </el-dialog>
      <div id="right" class="mt30">
        <right-view :uid="menuDetail.userid" v-if="menuDetail.userid"></right-view>
      </div>
    </div>
    <FooterBottom></FooterBottom>
    <Totop></Totop>
  </div>


</template>

<script>
  import headerNav from "@/components/headerNav";
  import RightView from "../components/rightView";
  import FoodItem from "@/components/foodItem";
  import Totop from '@/components/totop.vue';
  import FooterBottom from "@/components/footer";
  import InfiniteLoading from 'vue-infinite-loading';
  import {zpList} from '@/api/action'
  import {getMenuDetail} from '@/api/menu'
  import {commentList, pushReply, followUser, unfollowUser, collect, unCollect} from '@/api/operate'
  import {Message} from "element-ui";

  export default {
    name: "cookBook",
    components: {
      RightView,
      FoodItem,
      headerNav,
      FooterBottom,
      InfiniteLoading,
      Totop
    },
    data() {
      return {
        num: 13,
        page: 1,
        page2: 0,
        sortList: '',
        menuDetail: '',
        foodList: [],
        commentList: [],
        beuserid: 0,
        message: '',
        replyid: 0,
        replyuserid: 0,
        commentid: 0,
        message1: '',
        message2: '',
        token: '',
        cpid: '',
        dialogVisible: false,
        mainMenuList: [],
        loadMore: true,
        replyShow: false,
        reply2Show: false,
        input_placeholder: '来了说两句吧~',
        is_reply: false,

        osshost: process.env.UPLOAD_URL,
        listpic: '?x-oss-process=style/list-img',
        userface: '?x-oss-process=style/user-img',
        detailpic: '?x-oss-process=style/detail-img',
      }
    },
    created() {
      this.token = this.$store.state.user.token || '';
      this.cpid = this.$route.query.id;
      this.getDataInfo(this.cpid);
      this.getCommentList()
    },
    methods: {
      navToIndex(id) {
        this.$router.push({path: "/user", query: {id}})
      },
      getDataInfo(cpid) {
        getMenuDetail(cpid).then(response => {
          let res = response.data;
          if (res.success) {
            this.menuDetail = res.result;
            this.foodList = this.menuDetail.stuff.split('|');
            for (var j = 0; j < this.foodList.length; j++) {
              let array = [];
              array = this.foodList[j].split('#');
              this.foodList[j] = {
                name: array[0],
                num: array[1]
              }
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      loadMorep(){
          this.page++;
          this.getCommentList()
      },
      getCommentList() {
        commentList(this.cpid, 2, this.page).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length != 0) {
              this.commentList = this.commentList.concat(res.result);
            } else {
              this.loadMore = false;
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      comment(userid, commentid) {
      	//评论
      	if (this.token == '') {
      		navigateTo(`../login/pswLogin`);
      	} else {
      		this.replyShow = true; //调起input框
      		this.beuserid = userid;
      		this.commentid = commentid;
      	}
      },
      reply(commentid,userid,id,nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = 0;
        this.replyuserid = 0;
      	this.beuserid = userid;
      	this.commentid = id;
        console.log(this.commentid,this.beuserid);
      },
      reply2(id, topuserid, userid, commentid, nickname) {
      	this.is_reply = true; //回复中
      	this.replyShow = true; //调起input框
      	let replyTo = nickname;
      	this.input_placeholder = '回复' + replyTo;
      	this.replyid = id;
      	this.beuserid = topuserid;
      	this.replyuserid = userid;
      	this.commentid = commentid;
      },
      postComment() {
        if (this.token == '') {
          this.$router.push({path: "/login"})
        } else {
          let _this = this;
          let meassage = _this.message;
          let params = {
          	itemid: _this.cpid,
          	itemtype: 2,
          	replyid: _this.replyid,
          	replyuserid: _this.replyuserid,
          	commentid: _this.commentid,
          	beuserid: _this.beuserid,
          	message: _this.message
          };
          pushReply(params).then(response => {
            let res = response.data;
            if (res.success) {
              Message({
                message: res.message,
                duration: 5 * 1000
              })
              document.getElementById("reply").scrollIntoView();
              this.page = 0;
              this.commentList = [];
              this.getCommentList()
              this.is_reply = false; //回复中
              this.replyShow = false; //调起input框
            } else {
              Message({
                message: res.message,
                type: 'error',
                duration: 5 * 1000
              })
              if (res.code == '9999') {
                this.$router.push({path: "/login"})
              }
            }
          }).catch(error => {
            console.log(error)
          })
        }

      },
      collectMenu() {
        if (this.token) {
          if (this.menuDetail.iscollectioned) {
            unCollect(this.cpid, 2).then(response => {
              let res = response.data;
              if (res.success) {
                this.menuDetail.iscollectioned = false;
              } else {
                Message({
                  message: response.message,
                  type: 'error',
                  duration: 5 * 1000
                })
              }
            }).catch(error => {
              console.log(error)
            })
          } else {
            collect(this.cpid, 2).then(response => {
              let res = response.data;
              if (res.success) {
                this.menuDetail.iscollectioned = true;
              } else {
                Message({
                  message: response.message,
                  type: 'error',
                  duration: 5 * 1000
                })
              }
            }).catch(error => {
              console.log(error)
            })
          }
        } else {
          this.$router.push({path: "/login"})
        }
      },
      attentionUser(userid) {
        if (this.token) {
          followUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.menuDetail.relationship == 0) {
                this.menuDetail.relationship = 1;
              } else if (this.menuDetail.relationship == 2) {
                this.menuDetail.relationship = 3;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      unAttentionUser(userid) {
        if (this.token) {
          unfollowUser(userid).then(response => {
            let res = response.data;
            if (res.success) {
              if (this.menuDetail.relationship == 1) {
                this.menuDetail.relationship = 0;
              } else if (this.menuDetail.relationship == 3) {
                this.menuDetail.relationship = 2;
              }
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        } else {
          this.$router.push({path: "/login"})
        }
      },
      infiniteHandler($state) {
        this.page2++;
        this.getDtList($state)
      },
      getDtList($state) {
        zpList(this.cpid, this.page2).then(response => {
          let res = response.data;
          if (res.success) {
            if (res.result.length !== 0) {
              this.mainMenuList = this.mainMenuList.concat(res.result);
              for (var i = 0; i < this.mainMenuList.length; i++) {
                this.mainMenuList[i].dtimg = this.mainMenuList[i].dtimg.split('|');
                this.mainMenuList[i].dtimg = this.mainMenuList[i].dtimg[0];
              }
              if (this.page2 != 0) {
                $state.loaded();
              }
            } else {
              $state.complete();
            }
          } else {
            Message({
              message: response.message,
              type: 'error',
              duration: 5 * 1000
            })
          }
        }).catch(error => {
          console.log(error)
        })
      },
      navToDetail(id) {
        this.$router.push({path: "/dtDetail", query: {id: id}})
      },
    }
  }
</script>

<style scoped>
  .mainImg {
    width: 685px;
    height: 456px;
  }
  .grid{
    border:1px solid #eee;
  }
  .grid.col-2.border>div{
    border: 0;
    border-bottom: 1px solid #eee;
  }
  .grid.col-2.border>div:nth-child(odd){
    border-right: 1px solid #eee;
  }
</style>



